"use client";

import { useState, useTransition } from "react";
import { toast } from "sonner";
import { Button } from "@/components/ui/button";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";
import { MoreHorizontal, PlusCircle } from "lucide-react";
import { AspForm, aspFormSchema } from "./asp-form";
import { createAsp } from "@/actions/asp-actions";
import { z } from "zod";

// aspsテーブルの型を仮定
type Asp = {
  id: string;
  name: string;
  category: string | null;
  login_url: string | null;
  created_at: string;
  prompt: string | null;
};

interface AgentClientProps {
  data: Asp[];
}

export function AgentClient({ data }: AgentClientProps) {
  const [isCreateDialogOpen, setIsCreateDialogOpen] = useState(false);
  const [isPending, startTransition] = useTransition();

  const onSubmit = (values: z.infer<typeof aspFormSchema>) => {
    startTransition(async () => {
      const result = await createAsp(values);
      if (result.error) {
        toast.error(result.error);
      } else {
        toast.success(result.data);
        setIsCreateDialogOpen(false);
      }
    });
  };

  return (
    <>
      {/* Header */}
      <div className="flex items-center justify-between mb-6">
        <div>
          <h1 className="text-2xl font-bold tracking-tight">
            エージェント管理
          </h1>
          <p className="text-muted-foreground">
            AIエージェントが利用するASPの情報を管理します。
          </p>
        </div>
        <Dialog open={isCreateDialogOpen} onOpenChange={setIsCreateDialogOpen}>
          <DialogTrigger asChild>
            <Button>
              <PlusCircle className="mr-2 h-4 w-4" />
              新規ASPを登録
            </Button>
          </DialogTrigger>
          <DialogContent>
            <DialogHeader>
              <DialogTitle>新規ASPを登録</DialogTitle>
            </DialogHeader>
            <AspForm onSubmit={onSubmit} isPending={isPending} />
          </DialogContent>
        </Dialog>
      </div>

      {/* Table */}
      <div className="border rounded-lg">
        <Table>
          <TableHeader>
            <TableRow>
              <TableHead className="w-[200px]">ASP名</TableHead>
              <TableHead>カテゴリ</TableHead>
              <TableHead>ログインURL</TableHead>
              <TableHead className="w-[50px]"></TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {data.map((asp) => (
              <TableRow key={asp.id}>
                <TableCell className="font-medium">{asp.name}</TableCell>
                <TableCell>{asp.category}</TableCell>
                <TableCell>
                  <a
                    href={asp.login_url || "#"}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="text-blue-600 hover:underline"
                  >
                    {asp.login_url}
                  </a>
                </TableCell>
                <TableCell>
                  <DropdownMenu>
                    <DropdownMenuTrigger asChild>
                      <Button variant="ghost" className="h-8 w-8 p-0">
                        <span className="sr-only">メニューを開く</span>
                        <MoreHorizontal className="h-4 w-4" />
                      </Button>
                    </DropdownMenuTrigger>
                    <DropdownMenuContent align="end">
                      <DropdownMenuItem>編集</DropdownMenuItem>
                      <DropdownMenuItem className="text-red-500">
                        削除
                      </DropdownMenuItem>
                    </DropdownMenuContent>
                  </DropdownMenu>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </div>
    </>
  );
}
